<template>
  <div class="wrap-g" style="height: 100vh">
    <mt-header title="消息中心" style="background: #fff;color: #1B1B1B;">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <!--<mt-button icon="more" slot="right"></mt-button>-->
    </mt-header>
    <div class="main">
      <div class="item">
        <div class="time">
          2018年5月17日15:06
        </div>
        <div class="content" >

        </div>
      </div>
    </div>
    <ul class="chat-thread">
      <li class="right">
        <img src="../../../static/img/home/Advertisement.png" class="before">
        我是用边框法实对话框噢～</li>
      <li class="left">
        <img src="../../../static/img/home/Advertisement.png" class="before">
        我是用边框法对话框噢～</li>

    </ul>

  </div>

</template>

<script>
  export default {
    data () {
      return {

      }
    },
    methods:{
    },
    mounted(){

    }
  }
</script>


<style lang="less" scoped>
  .main{
  .item{
    margin: 25px 0;
  .time{
    text-align: center;
    font-family: MicrosoftYaHei;
    background: RGBA(201, 201, 201, 1);
    padding: 5px;
    color: #fff;
    width: 35%;
    margin: 0 auto;
    border-radius: 3px;
  }
  }
  }
  .chat-thread {    margin: 24px auto 0 auto;    padding: 0 20px 0 0;    list-style: none;    overflow-y: scroll;    overflow-x: hidden;}
  .chat-thread li {    position: relative;    clear: both;    display: inline-block;    padding: 16px 40px 16px 20px;    margin: 0 0 20px 0;    font: 16px/20px 'Noto Sans', sans-serif;    border-radius: 10px;    }
  .chat-thread li .before {    position: absolute;    top: 0;    width: 50px;    height: 50px;    border-radius: 50px;  }
  .chat-thread li:after {    position: absolute;    top: 15px;    content: '';    width: 0;    height: 0;    border-top: 15px solid  #ABD5FF;}
  .chat-thread li.left {    animation: show-chat-odd 0.15s 1 ease-in;    -moz-animation: show-chat-odd 0.15s 1 ease-in;    -webkit-animation: show-chat-odd 0.15s 1 ease-in;    float: right;    margin-right: 80px;    color: RGBA(67, 67, 67, 1); background-color: #ABD5FF;}
  .chat-thread li.left .before {    right: -80px;}
  .chat-thread li.left:after {    border-right: 15px solid transparent;    right: -15px;}
  .chat-thread li.right {    animation: show-chat-even 0.15s 1 ease-in;    -moz-animation: show-chat-even 0.15s 1 ease-in;    -webkit-animation: show-chat-even 0.15s 1 ease-in;    float: left;    margin-left: 80px;    color: RGBA(67, 67, 67, 1);}
  .chat-thread li.right .before {    left: -60px;}
  .chat-thread li.right:after {     left: -15px;}

</style>



